import React,{useState} from "react";



function Header(props){

    const [colors,setColor]=useState({
        backgroundColor: ""
    });


    function handleClick(event){
  const color = event.target.value
        if(color === "black"){
            return setColor({backgroundColor:" #212121"});
        }else if(color === "yellow"){
            return setColor({backgroundColor:"#ffd369"});  
        }else if(color === "pink"){
            return setColor({backgroundColor:"#e7d9ea"});
        }
        
    }
    props.colorChange(colors); 

return (
    <header style={colors} >
     <h1>React Note</h1>
     <button onClick={handleClick} className="black"  value="black"></button>
     <button onClick={handleClick} className="yellow" value="yellow"></button>
     <button onClick={handleClick} className="pink" value="pink"></button>
    </header>
)
}

export default Header;