import React,{useState} from "react";
import AddIcon from '@material-ui/icons/Add';

function Input(props){

    const[note,setNote]=useState({
title:"",
content:""
    });

    function eventHandeler(event){    
    const {name,value}= event.target;

        setNote(prevNote =>{
            return{...prevNote,
            [name]: value}
        });   
}

    function submitNote(event){
    props.onAdd(note)
    setNote({
        title:"",
        content:""
            });
    event.preventDefault();
    }

return(<form className="create-note">
<input name="title" value={note.title} onChange={eventHandeler} type="text"  autoComplete="off" placeholder="Title"></input> 
<textarea name="content" value={note.content} onChange={eventHandeler}  placeholder="Take a note..." rows="3"/>
<button style={props.fundal} onClick={submitNote}><AddIcon/></button>
</form>)
}

export default Input;